body {
  background-color: bisque;
}
.weather-page {
  font-family: "Manrope", sans-serif;
  max-width: 800px;
  margin: 0 auto;

  border-radius: 50%;
  padding: 10px;
}

.weather-details {
  padding: 40px;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.weather-details h1 {
  margin-bottom: 10px;
}
.weather-details h2 {
  margin-bottom: 10px;
}

.weather-details p {
  margin: 5px 0;
  padding: 10px;
}

.sunny {
  background-color: #ffeb3b; /* Yellow */
}

.cloudy {
  background-color: #87ceeb; /* Sky Blue */
}

.rainy {
  background-color: #49419e; /* Dark Blue */
}
