/* Existing styles */
.cities-table {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  background-color: antiquewhite;
}

input[type="text"] {
  width: calc(100% - 22px); /* Adjusting for padding and border */
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.suggestions {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.suggestions li {
  padding: 10px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}

.suggestions li:hover {
  background-color: #e0e0e0;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #f5f5f5;
}

.not-found-message {
  margin-top: 20px;
  font-style: italic;
  color: red;
}
.button {
  width: 110px;
  height: 30px;
  font-size: medium;
  font-weight: 600;
}
