.App {
  text-align: center;
}

.cities-table {
  margin: 20px auto;
  max-width: 800px;
}

.cities-table input {
  padding: 8px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
}

.cities-table table {
  width: 100%;
  border-collapse: collapse;
}

.cities-table th,
.cities-table td {
  padding: 10px;
  border: 1px solid #ddd;
}

.cities-table th {
  background-color: #f2f2f2;
}

.weather-page {
  margin: 20px auto;
  max-width: 800px;
}

.weather-page .weather-info {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
